// llmatch.js
// A JavaScript library for invoking LLMs and matching their responses with patterns
// Similar to the Python llmatch package
import { HumanMessage, SystemMessage, AIMessage } from "@langchain/core/messages";

/**
 * Invokes an LLM, retrying until a response matches the pattern or max_retries is reached.
 *
 * @param {Object} options - Configuration options
 * @param {Object} [options.llm=null] - An already initialized instance of the LLM client
 * @param {string} [options.query="Extract relevant data."] - The main query or instruction for the LLM
 * @param {string|RegExp} [options.pattern=/```(.*?)```/s] - A regex string or compiled RegExp object
 * @param {string} [options.context=null] - Optional additional text to provide context to the LLM
 * @param {string} [options.promptTemplate="{query}\n\n{context}\nWrite the answer in the next format: {format}"] - Template string
 * @param {number} [options.maxRetries=15] - Maximum number of attempts to make
 * @param {number} [options.initialDelay=1.0] - Seconds to wait before the first retry
 * @param {number} [options.backoffFactor=1.5] - Multiplier for the delay between retries
 * @param {boolean} [options.verbose=false] - If true, prints detailed logs of the process
 * @param {Object} [options.passThrough={}] - Additional parameters to pass to the LLM invocation
 * @returns {Promise<Object>} Result object containing success status, extracted data, and metadata
 */
async function llmatch({
  llm = null,
  query = "Extract relevant data.",
  pattern = /```(.*?)```/s,
  context = null,
  promptTemplate = "{query}\n\n{context}\nWrite the answer in the next format: {format}",
  maxRetries = 15,
  initialDelay = 1.0,
  backoffFactor = 1.5,
  verbose = false,
  passThrough = {},
  ...kwargs
} = {}) {
  // Validate and compile regex pattern if needed
  let compiledPattern;
  if (pattern) {
    if (typeof pattern === 'string') {
      try {
        compiledPattern = new RegExp(pattern, 's');
        if (verbose) {
          console.log(`Compiled regex pattern: ${pattern}`);
        }
      } catch (e) {
        if (verbose) {
          console.log(`Error: Invalid regex pattern provided: ${pattern}. Error: ${e}`);
        }
        return {
          success: false,
          extractedData: null,
          finalContent: null,
          retriesAttempted: 0,
          errorMessage: `Invalid regex pattern: ${e}`,
          rawResponse: null,
        };
      }
    } else if (pattern instanceof RegExp) {
      compiledPattern = pattern;
      if (verbose) {
        console.log(`Using provided compiled regex pattern: ${compiledPattern.source}`);
      }
    } else {
      compiledPattern = null;
      if (verbose) {
        console.log("No pattern provided. Will return first valid response.");
      }
    }
  } else {
    compiledPattern = null;
    if (verbose) {
      console.log("No pattern provided. Will return first valid response.");
    }
  }

  // Prepare prompt
  const contextStr = context !== null ? context : "";
  let fullPrompt;
  try {
    fullPrompt = promptTemplate
      .replace('{query}', query)
      .replace('{context}', contextStr)
      .replace('{format}', pattern instanceof RegExp ? pattern.toString() : String(pattern));
  } catch (e) {
    if (verbose) {
      console.log("Warning: promptTemplate is missing '{query}' or '{context}' or '{format}'. Using basic query.");
    }
    fullPrompt = query + (contextStr ? `\n\n${contextStr}` : "");
  }

  // Prepare messages
  const messages = [new HumanMessage(fullPrompt)];

  // Retry loop
  let retryCount = 0;
  let currentDelay = initialDelay;
  let lastError = null;
  let finalContent = null;
  let rawResponse = null;

  while (retryCount <= maxRetries) {
    const attempt = retryCount + 1;
    if (verbose) {
      console.log(`\n--- LLM Invocation: Attempt ${attempt}/${maxRetries + 1} ---`);
    }

    try {
      if (!llm) {
        throw new Error("No LLM instance provided. Please initialize an LLM client.");
      }

      // Invoke the LLM
      const response = await llm.invoke(messages, passThrough);
        if (verbose) {
            console.log(`LLM Response: ${JSON.stringify(response)}`);
        }
      rawResponse = response;

      // 1. Validate response structure
      if (!response || !response.content) {
        lastError = "Invalid response object structure received from LLM.";
        if (verbose) {
          console.log(`Error: ${lastError} Response: ${JSON.stringify(response)}`);
        }

        messages.push(new AIMessage(String(response)));
        messages.push(new HumanMessage(`Retrying due to error: ${lastError}`));

        retryCount++;

        if (retryCount <= maxRetries) {
          await sleep(currentDelay * 1000);
          currentDelay *= backoffFactor;
        }
        continue;
      }
      if (verbose) {
        console.log(`Response Structure: ${JSON.stringify(response)}`);
      }

      const content = response.content;
      finalContent = content; // Store last received content

      // 2. Validate content type
      if (typeof content !== 'string') {
        lastError = `LLM response content is not a string (type: ${typeof content}).`;
        if (verbose) {
          console.log(`Error: ${lastError} Content: ${content}`);
        }

        messages.push({ role: 'assistant', content: String(content) });
        messages.push({ role: 'user', content: `Retrying due to error: ${lastError}` });
        retryCount++;

        if (retryCount <= maxRetries) {
          await sleep(currentDelay * 1000);
          currentDelay *= backoffFactor;
        }
        continue;
      }

      if (verbose) {
        console.log(`Raw Response Content: ${content}`);
      }

      // 3. Match Pattern (if provided)
      if (compiledPattern) {
        const matches = content.match(compiledPattern);
        if (matches) {
          // In JavaScript, RegExp.match returns an array with the full match at index 0
          // and each capturing group starting at index 1
          const extractedData = matches.slice(1).length > 0 ? matches.slice(1) : [matches[0]];

          if (verbose) {
            console.log(`Success: Pattern '${compiledPattern.source}' found.`);
            console.log(`Extracted Data: ${extractedData}`);
          }

          return {
            success: true,
            extractedData: extractedData,
            finalContent: content,
            retriesAttempted: retryCount,
            errorMessage: null,
            rawResponse: rawResponse,
          };
        } else {
          lastError = `Pattern '${compiledPattern.source}' not found in response.`;
          if (verbose) {
            console.log(`Info: ${lastError}`);
          }

          messages.push({ role: 'assistant', content: content });
          messages.push({ role: 'user', content: `Retry due to error: ${lastError}` });
        }
      } else {
        // No pattern provided, first valid string response is success
        if (verbose) {
          console.log("Success: Valid string response received (no pattern matching required).");
        }

        return {
          success: true,
          extractedData: null, // No pattern to extract
          finalContent: content,
          retriesAttempted: retryCount,
          errorMessage: null,
          rawResponse: rawResponse,
        };
      }

    } catch (e) {
      lastError = `Exception during LLM invocation: ${e}`;
      if (verbose) {
        console.log(`Error: ${lastError}`);
      }

      messages.push({ role: 'assistant', content: String(e) });
      messages.push({ role: 'user', content: `Retrying due to error: ${lastError}` });
    }

    // Prepare for next retry
    retryCount++;
    if (retryCount <= maxRetries) {
      if (verbose) {
        console.log(`Retrying in ${currentDelay.toFixed(2)} seconds...`);
      }

      await sleep(currentDelay * 1000);
      currentDelay *= backoffFactor; // Increase delay for next time

      messages.push({ role: 'assistant', content: "Retrying..." });
      messages.push({ role: 'user', content: `Retry attempt ${attempt} due to error: ${lastError}` });
    }
  }

  // End of Retries
  if (verbose) {
    console.log("\n--- Max retries reached or loop exited ---");
  }

  return {
    success: false,
    extractedData: null,
    finalContent: finalContent, // Content from the very last attempt
    retriesAttempted: retryCount - 1, // How many retries were actually done
    errorMessage: lastError || "Max retries reached without success.",
    rawResponse: rawResponse, // Raw response from the very last attempt
  };
}

/**
 * Helper function to pause execution for a specified duration
 * @param {number} ms - Time to sleep in milliseconds
 * @returns {Promise<void>}
 */
function sleep(ms) {
  return new Promise(resolve => setTimeout(resolve, ms));
}

export { llmatch };
